<template>
  <div class="box" :class="animateClass">
    <Head title="搜索" :show="true" :search="true"></Head>
    <form action="/">
      <van-search v-model="keyword" show-action placeholder="请输入搜索关键词" @search="onSearch">
        <template #action>
          <div @click="todoSearch">搜索</div>
        </template>
      </van-search>
    </form>
  </div>
</template>

<script>
// Search 组件提供了search和cancel事件，
// search事件在点击键盘上的搜索/回车按钮后触发（传的是输入），cancel事件在点击搜索框右侧取消按钮时触发
import { animate } from "@/utils/index.js";
import { mapState } from "vuex";

export default {
  mixins: [animate],
  data() {
    return {
      // keyword: '',
    };
  },
  computed: {
    // ...mapState('search',['keyword'])
    keyword: {
      get() {
        return this.$store.state.search.keyword;
      },
      set(newVal) {
        this.$store.commit("search/changeKw", newVal); // 提交mutation 去修改 vuex 数据
      }
    }
  },
  methods: {
    onSearch(val) {
      //绑定的是回车事件
      console.log(val);
    },
    todoSearch(e) {
      console.log(e); //传的是事件
      console.log(this.keyword);
    }
  }
};
</script>